}

img {
  width: 20%;
  height: 20%;
  border-radius: 50%;
}

.modalBtn {
  margin: 10px
}

.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    border-radius: 4px;
    width: 80%;
    position: relative;
}

/* The Close Button */
.close {
    color: #aaaaaa;
    position: absolute;
    right: -3px;
    top: -6px;
    font-size: 28px;
    font-weight: bold;
}
.closeOffset {
  right: 13px;
  top: 19px;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

#mc_embed_signup{
